'use client'

import { useState, useEffect } from 'react'
import { webhooksApi, endpointsApi, ApiError } from '@/lib/api-client'
import { Webhook, Loader2, AlertCircle, Plus, Trash2, Edit2, CheckCircle, XCircle, Copy, Check } from 'lucide-react'

export default function WebhooksPage() {
  const [webhooks, setWebhooks] = useState<any[]>([])
  const [endpoints, setEndpoints] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [showCreate, setShowCreate] = useState(false)
  const [editingWebhook, setEditingWebhook] = useState<string | null>(null)
  const [copied, setCopied] = useState<string | null>(null)

  useEffect(() => {
    loadData()
  }, [])

  const loadData = async () => {
    try {
      setLoading(true)
      setError(null)
      
      const [webhooksData, endpointsData] = await Promise.all([
        webhooksApi.list(),
        endpointsApi.list(),
      ])
      
      setWebhooks(webhooksData.webhooks || [])
      setEndpoints(endpointsData.endpoints || [])
    } catch (err) {
      handleError(err)
    } finally {
      setLoading(false)
    }
  }

  const handleError = (err: any) => {
    if (err instanceof ApiError) {
      const errorMsg = err.status === 401 
        ? 'Invalid API key. Please check your API key and try again.'
        : `${err.message} (Status: ${err.status}, Code: ${err.code})`
      setError(errorMsg)
    } else if (err instanceof Error) {
      setError(`Failed to load data: ${err.message}`)
    } else {
      setError('Failed to load data: Unknown error')
    }
  }

  const handleDelete = async (id: string) => {
    if (!confirm('Are you sure you want to delete this webhook?')) return
    
    try {
      await webhooksApi.delete(id)
      loadData()
    } catch (err) {
      alert(err instanceof ApiError ? err.message : 'Failed to delete webhook')
    }
  }

  const copyToClipboard = (text: string, id: string) => {
    navigator.clipboard.writeText(text)
    setCopied(id)
    setTimeout(() => setCopied(null), 2000)
  }

  if (loading) {
    return (
      <div className="text-center py-12">
        <Loader2 className="w-8 h-8 animate-spin mx-auto text-lime-400" />
        <p className="mt-4 text-gray-400">Loading webhooks...</p>
      </div>
    )
  }

  return (
    <div>
      <div className="flex items-center justify-between mb-6">
        <h1 className="text-3xl font-bold">Webhooks</h1>
        <button
          onClick={() => setShowCreate(true)}
          className="flex items-center gap-2 px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors"
        >
          <Plus className="w-4 h-4" />
          Create Webhook
        </button>
      </div>

      {error && (
        <div className="mb-6 p-4 bg-red-900/20 border border-red-800 rounded-lg flex items-center gap-3">
          <AlertCircle className="w-5 h-5 text-red-400" />
          <span className="text-red-400">{error}</span>
        </div>
      )}

      {showCreate && (
        <CreateWebhookModal
          endpoints={endpoints}
          onClose={() => {
            setShowCreate(false)
            loadData()
          }}
        />
      )}

      {editingWebhook && (
        <EditWebhookModal
          webhook={webhooks.find(w => w.id === editingWebhook)}
          endpoints={endpoints}
          onClose={() => {
            setEditingWebhook(null)
            loadData()
          }}
        />
      )}

      {webhooks.length === 0 ? (
        <div className="text-center py-12 bg-gray-900 rounded-lg border border-gray-800">
          <Webhook className="w-12 h-12 text-gray-600 mx-auto mb-4" />
          <p className="text-gray-400 mb-4">No webhooks configured</p>
          <button
            onClick={() => setShowCreate(true)}
            className="px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors"
          >
            Create Your First Webhook
          </button>
        </div>
      ) : (
        <div className="space-y-4">
          {webhooks.map((webhook) => (
            <div key={webhook.id} className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <div className="flex items-start justify-between">
                <div className="flex-1">
                  <div className="flex items-center gap-3 mb-2">
                    <h3 className="text-xl font-semibold">{webhook.url}</h3>
                    {webhook.enabled ? (
                      <span className="px-2 py-1 text-xs rounded bg-green-900/30 text-green-400 flex items-center gap-1">
                        <CheckCircle className="w-3 h-3" />
                        Enabled
                      </span>
                    ) : (
                      <span className="px-2 py-1 text-xs rounded bg-gray-800 text-gray-400">
                        Disabled
                      </span>
                    )}
                  </div>
                  {webhook.endpointId && (
                    <p className="text-sm text-gray-400 mb-2">
                      Endpoint: {endpoints.find(e => e.id === webhook.endpointId)?.name || webhook.endpointId}
                    </p>
                  )}
                  <div className="flex flex-wrap gap-2 mt-3">
                    {webhook.events.map((event: string) => (
                      <span
                        key={event}
                        className="px-2 py-1 text-xs rounded bg-gray-800 text-gray-300"
                      >
                        {event}
                      </span>
                    ))}
                  </div>
                </div>
                <div className="flex gap-2 ml-4">
                  <button
                    onClick={() => setEditingWebhook(webhook.id)}
                    className="px-3 py-1.5 text-sm bg-gray-800 text-gray-300 rounded hover:bg-gray-700 transition-colors flex items-center gap-1"
                  >
                    <Edit2 className="w-4 h-4" />
                    Edit
                  </button>
                  <button
                    onClick={() => handleDelete(webhook.id)}
                    className="px-3 py-1.5 text-sm bg-red-900/20 text-red-400 rounded hover:bg-red-900/30 transition-colors flex items-center gap-1"
                  >
                    <Trash2 className="w-4 h-4" />
                    Delete
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  )
}

function CreateWebhookModal({ endpoints, onClose }: { endpoints: any[]; onClose: () => void }) {
  const [loading, setLoading] = useState(false)
  const [createdWebhook, setCreatedWebhook] = useState<any | null>(null)
  const [formData, setFormData] = useState({
    url: '',
    events: [] as string[],
    endpointId: '',
    enabled: true,
  })

  const eventTypes = [
    'payment.verified',
    'payment.failed',
    'usage.recorded',
    'session.completed',
    'endpoint.created',
    'endpoint.updated',
    'endpoint.deleted',
  ]

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    
    try {
      const data = await webhooksApi.create({
        url: formData.url,
        events: formData.events,
        endpointId: formData.endpointId || undefined,
        enabled: formData.enabled,
      })
      setCreatedWebhook(data)
    } catch (err) {
      alert(err instanceof ApiError ? err.message : 'Failed to create webhook')
    } finally {
      setLoading(false)
    }
  }

  const toggleEvent = (event: string) => {
    setFormData({
      ...formData,
      events: formData.events.includes(event)
        ? formData.events.filter(e => e !== event)
        : [...formData.events, event],
    })
  }

  if (createdWebhook) {
    return (
      <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
        <div className="bg-gray-900 rounded-lg border border-gray-800 p-6 max-w-md w-full mx-4">
          <h3 className="text-xl font-bold mb-4">Webhook Created!</h3>
          <p className="text-yellow-400 mb-4 text-sm">
            ⚠️ Save this secret securely. It will not be shown again.
          </p>
          {createdWebhook.secret && (
            <div className="bg-gray-800 rounded-lg p-4 mb-4">
              <div className="flex items-center justify-between mb-2">
                <span className="text-sm text-gray-400">Secret:</span>
                <button
                  onClick={() => {
                    navigator.clipboard.writeText(createdWebhook.secret)
                    alert('Copied to clipboard!')
                  }}
                  className="text-lime-400 hover:text-lime-300"
                >
                  <Copy className="w-4 h-4" />
                </button>
              </div>
              <code className="text-lime-400 break-all text-sm">{createdWebhook.secret}</code>
            </div>
          )}
          <button
            onClick={onClose}
            className="w-full px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors"
          >
            Done
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-gray-900 rounded-lg border border-gray-800 p-6 max-w-2xl w-full mx-4 max-h-[90vh] overflow-y-auto">
        <h3 className="text-2xl font-bold mb-6">Create Webhook</h3>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Webhook URL *
            </label>
            <input
              type="url"
              required
              value={formData.url}
              onChange={(e) => setFormData({ ...formData, url: e.target.value })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
              placeholder="https://your-webhook-url.com/webhook"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Endpoint (Optional)
            </label>
            <select
              value={formData.endpointId}
              onChange={(e) => setFormData({ ...formData, endpointId: e.target.value })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
            >
              <option value="">All endpoints</option>
              {endpoints.map((ep) => (
                <option key={ep.id} value={ep.id}>
                  {ep.name} ({ep.id})
                </option>
              ))}
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Events *
            </label>
            <div className="grid grid-cols-2 gap-2">
              {eventTypes.map((event) => (
                <label
                  key={event}
                  className="flex items-center gap-2 p-3 bg-gray-800 rounded-lg cursor-pointer hover:bg-gray-750 transition-colors"
                >
                  <input
                    type="checkbox"
                    checked={formData.events.includes(event)}
                    onChange={() => toggleEvent(event)}
                    className="w-4 h-4 text-lime-400 rounded focus:ring-lime-400"
                  />
                  <span className="text-sm">{event}</span>
                </label>
              ))}
            </div>
          </div>
          <div className="flex gap-3 pt-4">
            <button
              type="submit"
              disabled={loading || formData.events.length === 0}
              className="flex-1 px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors disabled:opacity-50"
            >
              {loading ? 'Creating...' : 'Create Webhook'}
            </button>
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 bg-gray-800 text-gray-300 rounded-lg hover:bg-gray-700 transition-colors"
            >
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}

function EditWebhookModal({ webhook, endpoints, onClose }: { webhook: any; endpoints: any[]; onClose: () => void }) {
  const [loading, setLoading] = useState(false)
  const [formData, setFormData] = useState({
    url: webhook?.url || '',
    events: webhook?.events || [],
    enabled: webhook?.enabled !== false,
  })

  const eventTypes = [
    'payment.verified',
    'payment.failed',
    'usage.recorded',
    'session.completed',
    'endpoint.created',
    'endpoint.updated',
    'endpoint.deleted',
  ]

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    
    try {
      await webhooksApi.update(webhook.id, formData)
      onClose()
    } catch (err) {
      alert(err instanceof ApiError ? err.message : 'Failed to update webhook')
    } finally {
      setLoading(false)
    }
  }

  const toggleEvent = (event: string) => {
    setFormData({
      ...formData,
      events: formData.events.includes(event)
        ? formData.events.filter((e: string) => e !== event)
        : [...formData.events, event],
    })
  }

  if (!webhook) return null

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-gray-900 rounded-lg border border-gray-800 p-6 max-w-2xl w-full mx-4 max-h-[90vh] overflow-y-auto">
        <h3 className="text-2xl font-bold mb-6">Edit Webhook</h3>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Webhook URL *
            </label>
            <input
              type="url"
              required
              value={formData.url}
              onChange={(e) => setFormData({ ...formData, url: e.target.value })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
            />
          </div>
          <div>
            <label className="flex items-center gap-2">
              <input
                type="checkbox"
                checked={formData.enabled}
                onChange={(e) => setFormData({ ...formData, enabled: e.target.checked })}
                className="w-4 h-4 text-lime-400 rounded focus:ring-lime-400"
              />
              <span className="text-sm text-gray-300">Enabled</span>
            </label>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Events *
            </label>
            <div className="grid grid-cols-2 gap-2">
              {eventTypes.map((event) => (
                <label
                  key={event}
                  className="flex items-center gap-2 p-3 bg-gray-800 rounded-lg cursor-pointer hover:bg-gray-750 transition-colors"
                >
                  <input
                    type="checkbox"
                    checked={formData.events.includes(event)}
                    onChange={() => toggleEvent(event)}
                    className="w-4 h-4 text-lime-400 rounded focus:ring-lime-400"
                  />
                  <span className="text-sm">{event}</span>
                </label>
              ))}
            </div>
          </div>
          <div className="flex gap-3 pt-4">
            <button
              type="submit"
              disabled={loading || formData.events.length === 0}
              className="flex-1 px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors disabled:opacity-50"
            >
              {loading ? 'Updating...' : 'Update Webhook'}
            </button>
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 bg-gray-800 text-gray-300 rounded-lg hover:bg-gray-700 transition-colors"
            >
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}






